
import VueCoreImageUpload  from 'vue-core-image-upload'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
name: 'goodsAdd',
directives: {
TransferDom
},
components: {
Previewer,
VueCoreImageUpload,
XInput,
Group,
Selector,
XSwitch
},
data () {
return {
specsList: [],
specsDataList: [{
name: '',
value: '',
specsList: []
}],
switchvalue: true,
list: [{key: 'gd', value: '广东'}, {key: 'gx', value: '广西'}],
defaultValue: '',
value: '',
images: [],
previewList: [],
uploadUrl: 'http://zayzsh.zbtdvip.com/api/merchant/good/add/goods/baseInfo',
params: {},
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
methods: {
addSpecial() {

},
specsChange(value) {
this.specsList.map((item)=>{
if(item.attrId === value) {
item.isReadonly = true
}
})
},
getBaseInfo() {
this.$http.fetchGet('/merchant/good/get/goods/baseInfo').then((res)=>{
this.specsList = res.data.data.goodsAttrItemDto
this.specsDataList[0].specsList = res.data.data.goodsAttrItemDto
})
},
deletePic(index) {
this.images.splice(index,1)
this.previewList.splice(index,1)
},
show (index) {
this.$refs.previewer.show(index)
},
imageuploaded(res) {
console.log(res)
},
imagechanged(data) {
let param = new FormData(); 
param.append('files',data);
this.$http.fetchPost('/merchant/common/image/upload',param,{
headers: {
"Content-Type": "multipart/form-data; boundary=----WebKitFormBoundaryJBcoeGdBCguPERbU"
}
}).then((res)=>{
this.images.push(res.data.data.path)
this.previewList.push({
src: res.data.data.path,
msrc: res.data.data.path
})
})
},
getBack() {
this.$router.go(-1)
},
},
created() {
this.getBaseInfo()
}
}



